
import { defineComponent, computed, ref, watch } from 'vue'
import {
    IonIcon,
    IonProgressBar,
    IonLabel,
    IonButton,
    IonPage,
    IonToolbar,
    IonHeader,
    IonTitle,
    IonContent,
    IonList,
    IonFooter,
    IonItem,
    IonBadge
} from "@ionic/vue"
import { Offline } from "@/composables/offline"
import {
    trash,
    checkmarkCircle
} from "ionicons/icons"
import { alertConfirmation } from '@/utils/Alerts'
import { SyncData } from '@/interfaces/Sync'

export default defineComponent({
    components: {
        IonIcon,
        IonProgressBar,
        IonBadge,
        IonLabel,
        IonButton,
        IonToolbar,
        IonList,
        IonItem,
        IonPage,
        IonHeader,
        IonTitle,
        IonContent,
        IonFooter
    },
    props: {
        id: {
            type: String,
            required: true
        },
        programID: {
            type: Number,
            required: true
        },
        onClose: {
            type: Function
        },
        onDelete: {
            type: Function
        },
        onSync: {
            type: Function
        }
    },
    setup(props) {
        const { offlineData, getOfflinePerson, isOnline, synchronizeRecord } = Offline()
        const synchronizationStarted = ref(false)
        const listData = computed(() => {
            if (!props.id) {
                return []
            }
            const person = getOfflinePerson(props.id)
            if (!person ||
                person.programID != props.programID ||
                !person.observationParams) {
                return []
            }
            return person.observationParams.map((obs: any) => ({
                ...obs,
                createdBy: person.createdBy
            }))
        })

        function close() {
            if (typeof props.onClose === 'function') {
                props.onClose()
            }
        }

        function removeEncounter(id: string) {
            alertConfirmation("Are you sure you want to delete observation?")
                .then((ok) => {
                    if (!ok) return
                    offlineData.value = offlineData.value.map((sync: SyncData) => {
                        if (sync.id === props.id && sync.observationParams) {
                            sync.observationParams = sync.observationParams
                                .filter((obs) => obs.id!= id)
                        }
                        return sync
                    })
                    if (typeof props.onDelete === 'function') {
                        props.onDelete()
                    }
                })
        }

        watch(isOnline, async (online) => {
            if (online) {
                synchronizationStarted.value = true
                try {
                    const updated = await synchronizeRecord(props.id)
                    if (updated.status === 'Success') {
                        offlineData.value = offlineData.value.filter((d: SyncData) => {
                            return d.id != props.id
                        })
                        if(typeof props.onSync === 'function') {
                            props.onSync(updated)
                        }
                    }
                } catch (e) {
                    console.error(`${e}`)
                }
                synchronizationStarted.value = false
            }
        }, { immediate: true })

        return {
            close,
            removeEncounter,
            listData,
            checkmarkCircle,
            trash,
            synchronizationStarted
        }
    }
})
