import { Service } from "@/services/service"
import { isOfflineMode, isOnline } from "./offline"

function getPropKey(prop: string) {
    return `userprop-${Service.getProgramID()}-${Service.getUserID()}-${prop}`
}

async function getUserProp(property: string) {
    if (isOfflineMode.value || !isOnline.value) {
        return sessionStorage.getItem(getPropKey(property))
    }
    try {
        const prop = await Service.getJson('user_properties', {
            property: property
        })
        return prop?.property_value
    } catch (e) {
        console.error(`${e}`)
    }
}

function setUserProp(property: string, data: any) {
    const activity = {
        property,
        property_value: data
    }
    sessionStorage.setItem(getPropKey(property), data)
    return Service.postJson('user_properties', activity)
}

export default function UserProperty() {
    return {
        getUserProp,
        setUserProp
    }
}