import { LabOrderOffline, ObservationOffline, SyncData, SyncVariable } from "@/interfaces/Sync";
import { AppInterface } from "@/apps/interfaces/AppInterface";
import { AppEncounterService } from "@/services/app_encounter_service";
import { toastSuccess } from "@/utils/Alerts";
import { randomUUID } from "@/utils/UUID";
import { ApiBusEvents } from "@/services/api_client";
import { Patientservice } from "@/services/patient_service";
import { LabOrderService } from "@/apps/ART/services/lab_order_service";
import { OrderService } from "@/services/order_service";
import { ConceptService } from "@/services/concept_service";
import HisApp from "@/apps/app_lib"
import SyncComponent from "@/components/SynData.vue"
import dayjs from "dayjs";
import SyncObsData from "@/components/SyncObsData.vue"
import EventBus from "@/utils/EventBus";
import isEmpty from "lodash/isEmpty"
import Apps from "@/apps/app_lib"
import { AppendleadingZero } from "@/utils/Strs";

export const isOnline = ref<boolean>(true)

export const isOfflineMode = ref<boolean>(`${sessionStorage.getItem("offline_mode")}` === 'true' ? true : false)

const obsDialogLaunched = ref<boolean>(false)

const syncDialogLaunched = ref<boolean>(false)

const synchronizationStarted = ref<boolean>(false)

const offlineData = ref<SyncData[]>([])

EventBus.on(ApiBusEvents.ON_API_CRASH, () => isOnline.value = false)

EventBus.on(ApiBusEvents.AFTER_API_REQUEST, () => isOnline.value = true)

/**
 * Finds offline record by offline ID or person id
 * @param id 
 * @returns 
 */
function getOfflinePerson(id: number|string) {
    const res = offlineData.value.find(d => d.id === id || d.personID === parseInt(`${id}`))
    if (res && !isEmpty(res?.personParams?.person)) {
        return { 
            ...res.personParams?.person, 
            npid: res.personParams?.npid,
            programID: res.programID, 
            createdBy: res.userName,
            observationParams: res.observationParams
        }
    }
}

/**
 * Builds default offline object and writes it to offline storage. 
 * @param override 
 * @returns 
 */
function saveOffline(override: any) {
    const app: AppInterface|undefined = HisApp.getActiveApp()
    if (app) {
        const defaults = {
            id: randomUUID(),
            label: "Offline",
            programID: app.programID,
            programName: app.applicationName,
            status: "Pending",
            type: "new",
            date: dayjs().format('DD/MM/YYYY hh:mm:ss'),
            userID: PatientRegistrationService.getUserID()||-1,
            userName: PatientRegistrationService.getUserName(),
        }
        offlineData.value.push({ ...defaults, ...override })
        return defaults.id
    }
    return ''
}

/**
 * Caches Patientservice object which is preset with person information.
 * @param patient 
 * @returns 
 */
function cachePatient(patient: Patientservice) {
    const id = patient.getID()
    const data = offlineData.value.find(d => d.id === id || d.personID === id)
    if (data) return data
    return saveOffline({
        id,
        label: "Returning client",
        type: "temp",
        personID: id,
        personParams: {
            npid: patient.getNationalID(),
            person: {
                age: patient.getAge(),
                birthdate: patient.getBirthdate(),
                gender: patient.getGender(),
                given_name: patient.getGivenName(),
                family_name: patient.getFamilyName(),
                phone: patient.getPhoneNumber(),
                current_village: patient.getCurrentVillage()
            }
        }
    })
}
/**
 * Base method for caching person information offline
 * @param label 
 * @param person 
 * @param attributes 
 * @param npid 
 * @returns 
 */
function savePersonOffline(label: string, person: Record<string, any>, attributes: Array<any>, npid=undefined as undefined|string) {
    return saveOffline({
        label,
        personParams: {
            npid,
            person,
            attributes
        }
    })
}

/**
 * Updates offline observations by writing them to existing users
 * @param params 
 * @returns 
 */
function saveObsOffline(params: ObservationOffline): string|number {
    if (!params.id) {
        return ''
    }
    const data = {
        ...params,
        id: randomUUID(),
        date: dayjs().format('DD/MM/YYYY hh:mm:ss')
    }
    offlineData.value.forEach((person) => {
        if ([`${params.id}`, `${params.patientID}`].includes(`${person.id}`)){
            if (Array.isArray(person.observationParams)) {
                person.observationParams.push(data)
            } else {
                person.observationParams = [data]
            }
        }
    })
    return params.id
}

async function launchSynchronizationDialog(autoSync=false) {
    if (syncDialogLaunched.value) return
    syncDialogLaunched.value = true;
    (await modalController.create({
        component: SyncComponent,
        backdropDismiss: false,
        cssClass: "large-modal",
        componentProps: {
            autoSync,
            onClose: () => {
                syncDialogLaunched.value = false
                modalController.getTop().then(m => m && modalController.dismiss())
            }
        }
    })).present()
}

async function launchObservationDialog(id: string|number, params={} as any) {
    if (obsDialogLaunched.value) return
    obsDialogLaunched.value = true;
    (await modalController.create({
        component: SyncObsData,
        backdropDismiss: false,
        cssClass: "large-modal",
        componentProps: {
            id,
            programID: PatientRegistrationService.getProgramID(),
            onDelete: () => {
                if (typeof params.onDeleteEncounter === 'function') {
                    params.onDeleteEncounter()
                }
            },
            onSync: (offline: any) => {
                if (typeof params.onSyncObservations === 'function') {
                    params.onSyncObservations(offline)
                }
            },
            onClose: () => {
                obsDialogLaunched.value = false
                modalController.getTop().then(m => m && modalController.dismiss())
            }
        }
    })).present()
}

/**
 * Create patient online
 */
async function registerClient(personData: Record<string, any>, attributes: any[]) {
    const person = new PatientRegistrationService()
    await person.registerBasicPatient(personData)
    if (attributes.length) {
        await person.createPersonAttributes(attributes)
    }
    return person.getPersonID()
}

/**
 * Create Lab orders online
 * @param personID 
 * @param userID 
 * @param labOrders 
 */
async function createLabOrders(personID: number, userID: number, labOrders: LabOrderOffline[]) {
    const order = new LabOrderService(personID, userID)
    const encounter = await order.createEncounter()
    if (!encounter) throw new Error("Unable to create order")
    const payload = labOrders.map((order) => {
        return {
            ...order,
            concept_id: typeof order.conceptName === 'string' 
                ? ConceptService.getCachedConceptID(order.conceptName) 
                : order.conceptName,
            specimenConcept: typeof order.specimenConceptName === 'string' 
                ? ConceptService.getCachedConceptID(order.specimenConceptName) 
                : order.specimenConceptName
        }
    })
    await OrderService.saveOrdersArray(encounter?.encounter_id, OrderService.buildLabOrders(encounter, payload))
    return true
}
/**
 * Create observations online
 * @param programID 
 * @param personID 
 * @param userID 
 * @param encounterTypeID 
 * @param observations 
 */
async function createObs(programID: number, personID: number, userID: number, encounterTypeID: number, observations: any) {
    const enc = new AppEncounterService(personID, userID)
    enc.programID = programID
    enc.encounterTypeID = encounterTypeID
    await enc.createEncounter()
    return enc.saveObservationList(observations)
}

async function cleanUpOfflineData() {
    offlineData.value = offlineData.value
        .map((offline) => {
            if (offline.observationParams) {
                offline.observationParams = offline.observationParams.filter((obs) => {
                    return !/success|Exception|cancel/i.test(`${obs.uploadStatus}`)
                })
            }
            return offline
        })
        .filter((offline) => {
            if (offline.type === 'temp' && isEmpty(offline.observationParams)) {
                return false
            }
            return true
        })
}

async function synchronizeRecord(offlineID: string|number) {
    const offline = offlineData.value.find(data => `${data.id}` === `${offlineID}`)
    if (!offline) {
        return
    }
    let personID = offline?.personID || null
    offline.status = "Synchronizing"
    offline.statusMessage = "Starting"
    // Search NPID to retrieve patient ID
    if (offline?.personParams?.npid) {
        offline.statusMessage = "Searching npid " + offline.personParams.npid
        try {
            const results = await Patientservice.findByNpid(offline.personParams.npid)
            if (Array.isArray(results) && 
                results.length <= 1) {
                personID = results[0].patient_id
            }
        } catch (e) {
            offline.statusMessage = "NPID not found"
            console.error(`${e}`)
        }
    }
    // Create New Patient in the system
    if (!personID && offline.personParams && offline.type != 'temp') {
        offline.statusMessage = "Registering patient record"
        try {
            personID = await registerClient(
                offline.personParams.person, 
                offline.personParams?.attributes||[]
            )
        } catch (e) {
            offline.status = "Error"
            offline.statusMessage = 'Registration failed'
            console.error(`${e}`)
            return offline
        }
    }
    // Save observations if set
    if (Array.isArray(offline.observationParams) && offline.observationParams.length) {
        offline.statusMessage = "Creating observations"
        const res = offline.observationParams.map(async (obs) => {
            if (obs.uploadStatus === 'Success') 
                return obs
            try {
                const obsCreated = await createObs(
                    offline.programID,
                    personID||-1,
                    offline.userID,
                    obs.encounterTypeID,
                    obs.observations
                )
                // Run observation lab orders
                if (Array.isArray(obs.labOrders)) {
                    offline.statusMessage = "Creating lab orders"
                    await createLabOrders(
                        personID||-1,
                        offline.userID,
                        obs.labOrders
                    )
                }
                if (obsCreated) obs.uploadStatus = 'Success'
            } catch (e) {
                if (/NetworkError|Failed to fetch/i.test(`${e}`)) {
                    obs.uploadStatus = 'NetworkError'
                } else {
                    obs.uploadStatus = 'Exception'
                }
                offline.status = "Error"
                offline.statusMessage = `Observation error: ${e}`
                console.error(`${e}`)
            }
            return obs
        })
        await Promise.all(res);
    }
    offline.personID = personID||offline?.personID
    offline.status = 'Success'
    offline.statusMessage = '-'
    return offline
}
/**
 * Uploads offline created persons, observations and lab orders
 * @returns 
 */
async function synchronize() {
    if (synchronizationStarted.value||!offlineData.value.length) 
        return
    toastSuccess("Synchronization started")
    for (let offline of offlineData.value) {
        synchronizationStarted.value = true
        if (offline.status === 'Success') {
            continue
        }
        offline = (await synchronizeRecord(offline.id))||offline
    }
    offlineData.value = offlineData.value.filter((d: SyncData) => d.status === 'Error')
    setTimeout(() => synchronizationStarted.value = false, 500)
    toastSuccess("Synchronization Complete")
    return offlineData.value
}

/**
 * Provides an offline workflow engine for determining next activity name based on PatientID
 * And activities that haven't been done for the patient.
 * today. 
 * @param patientID 
 * @returns 
 */
function nextPatientWorkflowActivity(patientID: string|number) {
    const app = Apps.getActiveApp()
    // Get offline data by patientID
    const offlineData = getOfflinePerson(patientID)
    if (!offlineData) return
    // Identify workflow tasks and sort them inorder by offlineWorkflowIndex
    const activities = (app?.primaryPatientActivites||[])
        .filter((activity) => typeof activity.offlineWorkflowIndex === 'number')
        .sort((a: any, b: any) => a.offlineWorkflowIndex - b.offlineWorkflowIndex)
        .filter((activity) => {
            // Ignore activities that were already done
            const taskDoneToday = (offlineData.observationParams||[])
                .find((obs: ObservationOffline) => {
                    if (obs.encounterName === activity.id)  {
                        // Format dates of format DD/MM/YYYY... i'd suggest revising a few things
                        const parts = `${obs.date}`.split('/');
                        const day = AppendleadingZero(parseInt(parts[0], 10));
                        const month = AppendleadingZero(parseInt(parts[1], 10)); // Month is zero-based in JavaScript (0-11)
                        const year = parseInt(parts[2], 10);
                        return `${year}-${month}-${day}` === Patientservice.getSessionDate()
                    }
                    return false
                })
            if (taskDoneToday) {
                return false
            }
            // For activities with workflow conditions, run validations to determine 
            // whether to carryout the activity or not
            if (typeof activity.offlineWorkflowCondition === 'function') {
                return activity.offlineWorkflowCondition({
                    ...offlineData, observationParams: (offlineData.observationParams||[])
                })
            }
            return true
        })
    return activities.length ? activities[0] : null
}


/**
 * Initiate in memory storage by retrieving data from offline storage
 */
if (!offlineData.value || !offlineData.value.length) {
    offlineData.value = JSON.parse(localStorage.getItem(SyncVariable.OfflineDB)||'[]')
}

/**
 * Updates offline storage everytime memory stored values change
 */
watch(offlineData, (data) => {
    localStorage.setItem(SyncVariable.OfflineDB, JSON.stringify(data))
}, { deep: true })

/**
 * Tracks offline mode in localstorage
 */
watch(isOfflineMode, (offline) => sessionStorage.setItem("offline_mode", `${offline}`))

export function Offline() : any {
    return {
        isOnline,
        isOfflineMode,
        offlineData,
        synchronize,
        saveObsOffline,
        cachePatient,
        getOfflinePerson,
        savePersonOffline,
        cleanUpOfflineData,
        synchronizeRecord,
        synchronizationStarted,
        launchObservationDialog,
        launchSynchronizationDialog,
        nextPatientWorkflowActivity
    }
}
